body {
  background-color: #ffb6c1;
}

.is-hidden {
  display: none;
}

.breed-select {
  width: 500px;
  margin-bottom: 30px;
  margin-left: auto;
  margin-right: auto;
}

.cat-info {
  width: 350px;
  background-color: #fff;
  border-radius: 10px;
  margin-left: auto;
  margin-right: auto;
  padding: 15px;
  box-shadow: 0 1px 6px rgba(46, 47, 66, .08), 0 1px 1px rgba(46, 47, 66, .16), 0 2px 1px rgba(46, 47, 66, .08);
}

.photo-of-cat {
  display: block;
}

.name-breed-cat {
  text-align: center;
}

.desc-cat {
  text-align: justify;
  margin-left: auto;
  margin-right: auto;
  font-size: 16px;
}

.temp-cat-second-part {
  font-weight: bold;
}

.loader {
  height: 50px;
  width: 50px;
  margin-left: auto;
  margin-right: auto;
  animation: rotate 3s infinite;
}

.loader:before, .loader:after {
  content: "";
  height: 20px;
  width: 20px;
  border-radius: 50%;
  display: block;
}

.loader:before {
  background-color: #fff;
  margin-bottom: 10px;
  animation: ball1 1s infinite;
  box-shadow: 30px 0 #db7093;
}

.loader:after {
  background-color: #db7093;
  animation: ball2 1s infinite;
  box-shadow: 30px 0 #fff;
}

@keyframes rotate {
  0% {
    transform: rotate(0deg)scale(.8);
  }

  50% {
    transform: rotate(360deg)scale(1.2);
  }

  100% {
    transform: rotate(720deg)scale(.8);
  }
}

@keyframes ball1 {
  0% {
    box-shadow: 30px 0 #db7093;
  }

  50% {
    margin-bottom: 0;
    transform: translate(15px, 15px);
    box-shadow: 0 0 #db7093;
  }

  100% {
    margin-bottom: 10px;
    box-shadow: 30px 0 #db7093;
  }
}

@keyframes ball2 {
  0% {
    box-shadow: 30px 0 #fff;
  }

  50% {
    margin-top: -20px;
    transform: translate(15px, 15px);
    box-shadow: 0 0 #fff;
  }

  100% {
    margin-top: 0;
    box-shadow: 30px 0 #fff;
  }
}

